import { Layout, Menu } from 'antd';
import style from './layout.module.scss'

import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';

const { Header, Footer, Sider, Content } = Layout;
const { SubMenu } = Menu;
const UrlRouter = {
  qrcodelist: '/manage/qrcode/list',
  userchangepwd: '/manage/user/changepwd'
}

const MainLayout = (props) => {

  const defaultOpenKeys = []
  const defaultSelectedKeys = []

  if(window.location.href.indexOf('qrcode') !== -1) {
    defaultOpenKeys.push('qrcode')
    defaultSelectedKeys.push('qrcodelist')
  }
  if(window.location.href.indexOf('user') !== -1) {
    defaultOpenKeys.push('user')
    defaultSelectedKeys.push('userchangepwd')
  }

  const handleClick = (e) => {
    const url = UrlRouter[e.key]
    window.location.href = url
  }
  return (
    <Layout className="mainLayout">
      <Header><h1>迪赛思中控小程序管理后台</h1></Header>
      <Layout>
        <Sider>
          <Menu
            onClick={handleClick}
            style={{ width: 200 }}
            defaultSelectedKeys={defaultSelectedKeys}
            defaultOpenKeys={defaultOpenKeys}
            mode="inline"
          >
            <SubMenu key="qrcode" icon={<SettingOutlined />} title="二维码管理">
              <Menu.Item key="qrcodelist">二维码列表</Menu.Item>
            </SubMenu>
            <SubMenu key="user" icon={<SettingOutlined />} title="账号管理">
              <Menu.Item key="userchangepwd">修改密码</Menu.Item>
            </SubMenu>
          </Menu>
        </Sider>
        <Content>
          {props.children}
        </Content>
      </Layout>
      <Footer>版权所有 © 2022 浙江冠轩智能科技有限公司</Footer>
    </Layout>    
  )

}
export default MainLayout