import React, { useState, useEffect } from "react";
import Layout from "../../layout/simpleLeftTop";
import {
  Form,
  Input,
  Button,
  Select,
  Row,
  Col,
  Card,
  Menu,
  Dropdown,
  Popconfirm,
  message,
} from "antd";
import { DownOutlined, AreaChartOutlined, MacCommandOutlined } from "@ant-design/icons";

import {apiAddQRCode, apiUpdateQRCode, apiGetQRCodeDetail} from '../../utils/apis'
import {getValueFromUrl} from '../../utils/utils'

const { TextArea } = Input;
const { Option } = Select;
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 12 },
};
const tailLayout = {
  wrapperCol: { offset: 2, span: 8 },
};

const Deal = (props) => {
  const id = getValueFromUrl('id')

  console.log("页面的props", props, id);
  const [pageType, setPageType] = useState(props.type);
  const [detail, setDetail] = useState({});
  const [config, setConfig] = useState([]);
  const [form] = Form.useForm();


  useEffect(() => {
    if (pageType === "edit") {
      const allRightType = {
        'button': true,
        'image': true
      }
      apiGetQRCodeDetail({id: id}).then(res => {
        console.log("获取详情", res);
        if(res.code === 0) {
          const d = res.data
          d.config = d.config || []
          d.config = d.config.filter(item => {
            return item && item.type && allRightType[item.type]
          })
          console.log('qrcode detail', d)
          setDetail(d)
          setConfig(d.config)
          form.setFieldsValue({
            ...d
          })
        }
      })
    }
  }, []);

  window.xxx = form;
  const onFinish = (values: any) => {
    // console.log(values);
    if (pageType === "edit") {
      const obj = {...values}
      obj.id = id
      // console.log("获取详情"); 编辑保存
      apiUpdateQRCode(obj).then(res => {
        if(res.code === 0) {
          message.success('编辑成功')
          setTimeout(() => {
            window.location.href = './list'
          }, 2000)
        } else {
          message.error('编辑失败：' + res.msg)
        }
      })
    } else {
      // 新增保存
      apiAddQRCode(values).then(res => {
        if(res.code === 0) {
          message.success('添加成功')
          setTimeout(() => {
            window.location.href = './list'
          }, 2000)
        } else {
          message.error('添加失败：' + res.msg)
        }
      })
    }
  };

  const onReset = () => {
    form.resetFields();
  };

  const handleAddConfigButtonMenuClick = (type) => {
    var confs = JSON.parse(JSON.stringify(config));
    console.log("点击了添加按钮", type.key);
    confs.push({
      type: type.key,
    });
    setConfig(confs);
  };
  const deleteConfig = (idx) => {
    let confs = JSON.parse(JSON.stringify(config));
    confs.splice(idx, 1);
    let datanow = JSON.parse(JSON.stringify(form.getFieldsValue()));
    datanow.config.splice(idx, 1);
    form.setFieldsValue(datanow);
    setConfig(confs);
  };
  const addConfigButtonMenu = (
    <Menu onClick={handleAddConfigButtonMenuClick}>
      <Menu.Item key="button" icon={<MacCommandOutlined />}>
        按钮组件
      </Menu.Item>
      <Menu.Item key="image" icon={<AreaChartOutlined />}>
        图片组件
      </Menu.Item>
    </Menu>
  );

  return (
    <Layout>
      <h2>{pageType === 'edit' ? '编辑二维码': '新增二维码'}</h2>
      <Row>
        <Col span={12}>
          <Form
            {...layout}
            form={form}
            name="control-hooks"
            onFinish={onFinish}
          >
            <Form.Item
              name="name"
              label="二维码名字"
              rules={[{ required: true }]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              name="deviceId"
              label="设备id"
              rules={[{ required: true, max: 32, min: 8 }]}
            >
              <Input placeholder="用于链接服务器时的topic话题"/>
            </Form.Item>
            <Form.Item name="secret" label="二维码密码">
              <Input placeholder="非必填，默认无密码" />
            </Form.Item>
            <Form.Item name="remark" label="二维码备注">
              <Input placeholder="仅显示在列表用于说明" />
            </Form.Item>
            <Form.Item
              name="company"
              label="所属公司"
              rules={[{ required: true }]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              name="meetingRooms"
              label="所属办公室"
              rules={[{ required: true }]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              name="title"
              label="预览页大标题"
            >
              <Input />
            </Form.Item>
            {/* <Form.Item
              name="desc"
              label="预览页文案描述"
            >
              <Input placeholder="文章摘要，建议20个字以上"/>
            </Form.Item> */}
            <Form.Item
              name="bg"
              label="预览页背景图片"
            >
              <Input placeholder="请输入完整的图片URL地址"/>
            </Form.Item>

            {config &&
              config.map((item, idx) => {
                const buttonDom = (
                  <Card
                    key={idx}
                    title={`按钮-${idx + 1}-配置`}
                    extra={<a onClick={() => deleteConfig(idx)}>删除</a>}
                    style={{ marginBottom: 8 }}
                  >
                    <Form.Item
                      hidden={true}
                      name={["config", idx, "type"]}
                      label="类型"
                      rules={[{ required: true }]}
                      initialValue="button"
                    >
                      <Input value="button" />
                    </Form.Item>
                    {/* <Form.Item
                      name={["config", idx, "title"]}
                      label="按钮说明"
                      rules={[{ required: true }]}
                    >
                      <Input />
                    </Form.Item> */}
                    <Form.Item
                      name={["config", idx, "text"]}
                      label="按钮显示文案"
                      rules={[{ required: true }]}
                    >
                      <Input />
                    </Form.Item>
                    <Form.Item
                      name={["config", idx, "instructions"]}
                      label="按钮指令内容"
                      rules={[{ required: true }]}
                    >
                      <TextArea />
                    </Form.Item>
                  </Card>
                );
                const imageDom = (
                  <Card
                    key={idx}
                    title={`图片-${idx + 1}-配置`}
                    extra={<a onClick={() => deleteConfig(idx)}>删除</a>}
                    style={{ marginBottom: 8 }}
                  >
                    <Form.Item
                      hidden={true}
                      name={["config", idx, "type"]}
                      label="类型"
                      rules={[{ required: true }]}
                      initialValue="image"
                    >
                      <Input value="image" />
                    </Form.Item>
                    <Form.Item
                      name={["config", idx, "url"]}
                      label="图片互联网地址"
                      rules={[{ required: true }]}
                    >
                      <Input />
                    </Form.Item>
                  </Card>
                );
                const txt = {
                  button: buttonDom,
                  image: imageDom,
                };
                return txt[item.type];
              })}
            <Form.Item>
              <Dropdown
                overlay={addConfigButtonMenu}
              >
                <Button>
                  添加组件 <DownOutlined />
                </Button>
              </Dropdown>
            </Form.Item>

            <Form.Item {...tailLayout}>
              <Button type="primary" htmlType="submit">
                保存
              </Button>
              <Popconfirm
                title="确定要清空所有配置内容吗？"
                onConfirm={() => {
                  onReset();
                }}
                onCancel={() => {}}
                okText="是"
                cancelText="否"
              >
                <Button style={{ marginLeft: 24 }} htmlType="button">
                  重置
                </Button>
              </Popconfirm>
            </Form.Item>
          </Form>
        </Col>
        <Col span={8}></Col>
      </Row>
    </Layout>
  );
};
export default Deal;
