import Layout from "../../layout/simpleLeftTop";
import { Form, Input, Button, Checkbox, message } from 'antd';
import { apiChangePwd } from '../../utils/apis'

const ChangePwd = () => {
  const onFinish = (values: any) => {
    console.log('Success:', values);
    const obj = {
      ...values
    }
    apiChangePwd(obj).then(res => {
      if(res.code === 0) {
        message.success('修改成功，请重新登录')
        setTimeout(() => {
          window.localStorage.removeItem('token')
          window.location.href = '/manage/login'
        }, 2000)
      } else {
        message.error('修改密码失败' + res.msg)
      }
    })
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <Layout>
      <h2>修改密码</h2>
      <Form
        name="basic"
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 8 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label="旧用户名"
          name="oldname"
          rules={[{ required: true, max:128, message: '请输入现在的用户名!' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="旧密码"
          name="oldpwd"
          rules={[{ required: true, max:128, message: '请输入现在的密码!' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          label="新用户名"
          name="newname"
          rules={[{ required: true, max:128, message: '请输入新的用户名!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="新密码"
          name="newpwd"
          rules={[{ required: true, max:128, message: '请输入新的密码!' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 4, span: 16 }}>
          <Button type="primary" htmlType="submit">
            提交
          </Button>
        </Form.Item>
      </Form>
    </Layout>
  )
}
export default ChangePwd