const formatTime = (timestramp, format) => {
  console.log('timestramp', timestramp)
  timestramp = timestramp || new Date().getTime()
  format = format || 'YYYY-MM-DD hh:mm:ss'
  const time = new Date(timestramp);
  const y = time.getFullYear();
  const m = addZero(time.getMonth() + 1);
  const d = addZero(time.getDate());
  const h = addZero(time.getHours());
  const mm = addZero(time.getMinutes());
  const s = addZero(time.getSeconds());
  return format.replace('YYYY', y).replace('MM', m).replace('DD', d).replace('hh', h).replace('mm', mm).replace('ss', s)
}

const addZero = (n) => {
  return ('000'+n).slice(-2)
}

const getValueFromUrl = (key) => {
  let urlParams = window.location.search.replace('?', '')
  const urlParamsPbj = {}
  urlParams.split('&').map(item => {
    const tmp = item.split('=')
    urlParamsPbj[tmp[0]] =  tmp[1]
    return {}
  })
  return urlParamsPbj[key]
}
export {formatTime, getValueFromUrl}