import React, { useState, useEffect } from "react";
import Layout from "../../layout/simpleLeftTop";
import { Table, Tag, Space, Button, message, Popconfirm } from "antd";
import { formatTime } from "../../utils/utils";
import { apiGetQRCodeList, apiDeleteQRCode } from "../../utils/apis";
import Modal from "antd/lib/modal/Modal";

const List = () => {
  const [showQrModal, setShowQrModal] = useState(false);
  const [showQrContent, setShowQrContent] = useState(false);
  const showCode = (id) => {
    setShowQrContent(id);
    setShowQrModal(true);
    setTimeout(()=> {
      document.getElementById("qrcode").innerHTML = ''
      /* eslint-disable-next-line */
      new QRCode(document.getElementById("qrcode"), {
        text: window.location.origin + "/h5/detail?id=" + id,
        width: 128,
        height: 128,
        colorDark: "#000000",
        colorLight: "#ffffff",
        /* eslint-disable-next-line */
        correctLevel: QRCode.CorrectLevel.H,
      });      
    }, 100)
  };
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);
  const [listData, setListData] = useState([]);

  useEffect(() => {
    getListData()
  }, [page]);
  const getListData = () => {
    apiGetQRCodeList({ page, pageSize }).then((res) => {
      if (res.code === 0) {
        setListData(res.data);
        setTotal(res.total);
      }
    });
  }
  const columns = [
    {
      title: "名字",
      dataIndex: "name",
      key: "name",
      width: 220,
      fixed: "left",
      render: (text) => <a>{text}</a>,
    },
    {
      title: "设备ID",
      dataIndex: "deviceId",
      key: "deviceId",
      width: 220,
    },
    {
      title: "所属公司",
      dataIndex: "company",
      key: "company",
      width: 200,
    },
    {
      title: "所属办公室",
      dataIndex: "meetingRooms",
      key: "meetingRooms",
      width: 140,
    },
    {
      title: "创建时间",
      dataIndex: "createTime",
      key: "createTime",
      width: 240,
      render: (text, record) => {
        return formatTime(record.createTime);
      },
    },
    {
      title: "最后更新时间",
      dataIndex: "updateTime",
      key: "updateTime",
      width: 240,
      render: (text, record) => {
        return record.updateTime ? formatTime(record.updateTime) : "-";
      },
    },
    {
      title: "加密",
      dataIndex: "secret",
      render: (text, record) => {
        return record.secret ? "是" : "否";
      },
    },
    {
      title: "操作",
      key: "action",
      fixed: "right",
      width: 180,
      render: (text, record) => (
        <Space size="middle">
          <a href={`/manage/qrcode/edit?id=${record._id}`}>编辑</a>
          <a onClick={() => showCode(record._id)}>二维码</a>
          <Popconfirm
            title="确定删除吗？"
            okText="确定"
            cancelText="取消"
            // visible={visibleDeleteQrcode}
            onConfirm={() => deleteCode(record._id)}
            // okButtonProps={{ loading: confirmLoading }}
            // onCancel={() => setVisibleDeleteQrcode(false)}
          >
            <a >删除</a>
          </Popconfirm>
        </Space>
      ),
    },
  ];
  const [visibleDeleteQrcode, setVisibleDeleteQrcode] = useState(false)

  const deleteCode = (id) => {
    apiDeleteQRCode({id}).then(res => {
      if(res.code === 0) {
        message.success('删除成功')
        getListData()
      } else {
        message.error(res.msg || '删除失败')
      }
    })
  }
  const pageOnChange = (page) => {
    setPage(page);
  };
  return (
    <Layout>
      <h2>二维码管理</h2>
      <Button type="primary" style={{ marginBottom: 12 }} href="./add">
        新增二维码
      </Button>
      <Table
        rowKey="_id"
        scroll={{ x: 500 }}
        columns={columns}
        dataSource={listData}
        pagination={{
          onChange: pageOnChange,
          current: page,
          total: total,
          pageSize: pageSize,
        }}
      />

      <Modal
        visible={showQrModal}
        onOk={() => {
          setShowQrModal(false);
        }}
        onCancel={() => {
          setShowQrModal(false);
        }}
        cancelText="关闭"
        okText="确定"
      >
        {/* 二维码{showQrContent} */}
        <div id="qrcode"></div>
      </Modal>
    </Layout>
  );
};
export default List;
